@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600&display=swap";
body {
  color: #032539;
  background-color: #fcf5ef;
  font-family: Inter, sans-serif;
}

.main {
  padding: 1rem;
}

img {
  width: 100%;
}

.header {
  height: 100vh;
  background-image: linear-gradient(#0009, #0009), url("bg.bb9038da.jpg");
  background-size: cover;
}

.logo {
  filter: brightness(0) invert();
}

.fa-solid {
  color: #6db5ca;
  position: fixed;
  top: 0;
  right: 0;
  transform: translate(-20%, 20%);
}

.popular__movies {
  color: #555;
  background-color: #b8d8e0;
}

.popular__movies h3 {
  color: #222;
}

.btn-xs {
  color: #fff;
  background-color: #6db5ca;
  border: none;
}

.btn-xs:hover {
  background-color: #1c768f;
}

.movie-container__front-title {
  width: 100%;
  color: #002c3e;
  background-color: #f8e8da;
  border-radius: 0 0 10px 10px;
  padding: 1rem;
}

.movie-container__front img {
  border-radius: 10px 10px 0 0;
}

.carousel {
  border-radius: 10px;
}

.btn-secondary {
  color: #fcf5ef;
  background-color: #6db5ca;
}

.btn-secondary:hover {
  background-color: #1c768f;
}

.main-heading {
  color: #fcf5ef;
  text-transform: uppercase;
  font-size: 1.875rem;
  font-weight: 300;
  line-height: 1;
}

.sub-heading {
  position: relative;
}

.sub-heading:after {
  content: "";
  width: 4rem;
  border-bottom: 1px solid #032539;
  position: absolute;
  bottom: 0;
  transform: translateY(.5rem);
}

.input {
  width: 50%;
  color: #fcf5ef;
  border-color: #6db5ca;
  position: absolute;
  left: 49%;
  transform: translateY(30%);
}

.carousel-item {
  display: grid;
}

.discover__movie-container {
  overflow: hidden;
}

.carousel-item img {
  width: 100%;
}

@media (min-width: 48em) {
  .input {
    visibility: visible;
    top: 0;
    left: 40%;
  }

  .fa-solid {
    top: 1%;
  }

  .movie-container__front-title {
    width: 70%;
  }

  .carousel-item {
    grid-template-columns: 1fr 1fr;
  }

  .discover__movie-container img {
    width: 70%;
  }

  .carousel-item img {
    width: 60%;
  }
}

